body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ededed;
  margin: 0;
  padding: 0;
  font-size: 16px;
  /* font-family: Verdana, Tahoma; */
  letter-spacing: 1px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --content-link-c: rgb(0, 110, 255);
  --main-bc: #008080;
  --main-fc: #fff;
}

.main-bc {
  background: var(--main-bc);
  background-color: var(--main-bc);
}

.main-fc {
  color: var(--main-fc);
}

.current {
  background-color: #055;
  border-radius: 5px;
  padding: 5px;
  color: #fff;
}

.current:hover {
  background-color: #042f33;
  color: #fff;
}

.b-current {
  color: #000;
  text-shadow: 1px 1px 1px #00eeff;
  font-weight: bold;
}

.sqr-100 {
  width: 100px;
  height: 100px;
}

.sqr-100 img {
  width: 100px;
  height: 100px;
}


.main-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #008080;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
}

.main-nav-holder {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.main-nav a {
  text-decoration: none;
  margin-left: 20px;
  color: #fff;
  white-space: nowrap;
}

.nav a:hover {
  text-decoration: underline;
}

article nav a, section nav a {
  color: var(--content-link-c);
}

main {
  margin-top: 60px;
}

table, tr, th, td {
  border: 1px solid #033;
  border-collapse: collapse;
}

.table-holder {
  width: 100%;
  overflow: scroll;
}

th, td {
  padding: 4px;
}

td {
  vertical-align: top;
}

#my-apps {
  margin: 5px 15px;
  min-width: 1200px;
}

#my-apps td.des {
  width: 30%;
}

#my-apps th {
  background: #033;
  border-color: black;
  color: #fff;
}

#my-apps td img {
  width: 80px;
  height: 80px;
  margin: 0;
  padding: 0;
}

#my-apps td.icon {
  vertical-align: middle;
}

.page-header {
  margin-top: 60px;
  background: var(--bc);
  padding: 5px;
  text-align: center;
  color: var(--fc);
}

.blogs, .source-codes {
  margin: 20px 20px 50px 20px;
}

.sc-wrapper textarea {
  width: 100%;
  height: auto;
  min-height: 500px;
  border: none;
  resize: none;
  padding: 3px;
}

.sc-nav {
  display: flex;
  flex-direction: row;
  margin-bottom: 9px;
}

.sc-nav div {
  margin-left: 15px;
  background: #033;
  color: #fff;
  padding: 5px 9px;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0px 0px 3px 2px green;
}

.sc-nav div.btn {
  background: #1831f1;
  border-radius: 2px;
  padding: 6px 11px;
  box-shadow: 0px 0px 3px 2px #267df8;
}

.sc-nav div.c {
  background: #00eeff;
  color: #033;
}

.feature-hdr {
  margin-top: 66px;
  padding: 150px 0 200px 0;
  background-color: #008080;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}

#questions .questions-form-holder {
  width: 100%;
  height: auto;
}

#questions iframe {
  width: 100%;
  height: 990px;
}

/* footer */

footer.footer {
  background: #008080;
  padding: 2vw 4.2vw;
  width: auto;
  color: #fff;
}

footer.footer section.container-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

footer.footer section.container-footer section {
  display: flex;
  flex-direction: column;
}

footer.footer section.container-footer section div {
  font-weight: bold;
}

footer.footer section.container-footer section a {
  text-align: left;
  text-decoration: none;
  color: #fff;
  margin-top: 11px;
  font-size: 12px;
}

footer div.copyright {
  text-align: center;
  margin-top: 28px;
  font-size: 0.7rem;
}

/* End footer */

.source-space {
  margin-top: 250px;
}

@media only screen and (max-width: 550px) {

  .main-nav-holder {
    justify-content: space-around;
  }

  .main-nav a {
    margin-left: 0px;
  }

  #questions iframe {
    height: 1200px;
  }

  .source-space {
    margin-top: 320px;
  }
}

